//   font-size: calc(24px + 67 * ((100vw - 320px) / (1350 - 320))); адаптивный шрифт

.btn {
    &--primary {
        padding: 15px 40px;
        background-color: #05297F;
        border-radius: 50px;
        color: var(--color-white);
        font-size: 18px;
        line-height: 18px;
        transition: background-color .3s ease;
        outline: none;

        &:hover {
            background-color: #0838a7;
        }

        &.focus-visible {
            background-color:#0838a7 ;
        }
    }

    &__nav {
    

        svg {
            width: 24px;
            height: 24px;
            fill: #000000;
        }

        &-user {
            display: flex;
            align-items: center;

            span {
                padding-left: 10px;
                position: relative;

                &::after {
                    content: "";
                    position: absolute;
                    top: 3px;
                    right: -12px;
                    z-index: 1;
                    background-image: url("/img/up-chevron.svg");
                    width: 8px;
                    height: 8px;

                }
            }
            
        }


        &-cart {
            position: relative;

            &::after {
                content: "15";
                position: absolute;
                bottom: 0;
                right: -4px;
                background-color: var(--color-accent);
                color: var(--color-white);
                font-weight: 600;
                font-size: 11px;
                line-height: 11px;
                width: 17px;
                height: 17px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                text-align: center;
                justify-content: center;
            }
        }

        &.focus-visible {
            outline: 1px solid var(--color-accent);
            outline-style: 10px;
        }

    }


    &__signup {
        display: inline-block;
        background-color: #FC9D9D;
        border-radius: 50px;
        font-size: 18px;
        line-height: 18px;
        color: var(--color-white);
        padding: 12px 30px;
        transition: background-color .3s ease;
        &:hover {
            background-color: #f8b6b6;
        }
    }

    &__product {
        border-radius: 50px;
        background-color: var(--color-white);
        width: 34px;
        height: 34px;
        display: inline-flex;
        text-align: center;
        justify-content: center;
        align-items: center;


        svg {
            width: 18px;
            height: 18px;
        }
    }

    &__shop {
        display: inline-flex;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    &__buy {
        display: inline-block;
        padding: 8px 10px;
        text-transform: uppercase;
        font-weight: 300;
        font-size: 12px;
        line-height: 12px;

        &--coral {
            background-color: var(--color-secondary);

            &:hover {
                background-color: #f8b6b6;
            }

            &.focus-visible {
                background-color:#f8b6b6 ;
            }
        }
    }

}