@mixin desktop-lg {
  @media (max-width: 1200px) {
    @content;
  }
}

@mixin desktop-sm {
  @media (max-width: (1025px)) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin tablet-lg {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin tablet-ld {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin mobile-sm {
  @media (max-width: (320px)) {
    @content;
  }
}
